      <div class="about_nav">
        <div class="about_nav_btn" @click="goBack"><i class="el-icon-back"></i></div>
        <div class="about_nav_title">{{$t('About Us')}}</div>
        <div class="about_nav_text">
          <router-link to="/"><i class="el-icon-s-home"></i></router-link>
        </div>
      </div>
      <div class="about_content">
        <div>{{$t('AboutText1')}}</div>
        <div>{{$t('AboutText2')}}</div>
        <div>{{$t('AboutText3')}}</div>
      </div>
  </div>

</template>

<script>
import Enu from 'enujs';
export default {
    data () {
      return {

      }
    },
    created(){

    },

    methods:{

      goBack(){
        window.history.go(-1);
      },
      goNet(){
         window.open("https://web.lichang.io/", "_blank");
      }
    },
    computed: {

    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
    @rem: round(1/75*2,6)*1rem;
    .about_box{
      width: 100%;
      height: 100%;
      .about_nav{
          width: 100%;
          height: 44*@rem;
          line-height: 44*@rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 0 15*@rem;
          background: #ce2344;
          box-sizing: border-box;
          .about_nav_btn{
            width: 30*@rem;
            height: 44*@rem;
            display: flex;
            align-items: center;
            i{
              color: #fff;
              font-size: 25*@rem;
            }
          }
          .about_nav_title{
              font-size: 12*@rem;
              line-height: 44*@rem;
              color: #fff;
          }
          .about_nav_text{
              a{
                width: 30*@rem;
                height: 44*@rem;
                display: flex;
                align-items: center;
                text-align: right;
                i{
                  color: #fff;
                  font-size: 23*@rem;
                }
              }

          }
      }
      .about_content{
        padding: 24*@rem 16*@rem;
        line-height: 24*@rem;
        box-sizing: border-box;
        div{
          margin-bottom: 24*@rem;
          font-size: 14*@rem;
          color: #6b5334;
        }
      }
    }

</style>



